.main-page {
  color: $white;
  flex-wrap: wrap;
  .card {
    padding: 1% 2%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    flex-basis: 23%;
    height: clamp(100px, 55vh, 1000px);
    margin-bottom: 1%; }
  .first {
    text-align: center;
    background-image: url('../img/borsh.jpg');
    background-size: cover;
    position: relative;
    & h3 {
      margin-top: 40%;
      font-weight: 500; }
    .substrate {
      width: 100%;
      height: 100%;
      background-color: #000000af;
      position: absolute;
      top: 0;
      left: 0; } }
  .second {
    text-align: center;
    background-image: url('../img/borsh.jpg');
    background-size: cover;
    position: relative;
    font-size: 0.8rem;
    & h3 {
      font-size: 1.1rem;
      margin-bottom: 2%; }
    & .btn {
      font-size: 0.8rem; }
    .substrate-2 {
      width: 85%;
      height: 40%;
      background-color: #000000af;
      position: absolute;
      top: 55%;
      left: 10%; } }
  .third {
    & h3 {
      color: $brown;
      margin-bottom: 2%;
      margin-top: 4%;
      text-align: center; }
    & p {
      color: #5c6b80; }
    & .btn {
      margin-left: 29%;
      margin-right: 20%; } } }
